<template>
  <div class="map">
    <l-map :zoom="zoom" :center="center" ref="map" style="height:100%;">
      <l-tile-layer :url="url" :attribution="attribution"></l-tile-layer>
      <l-marker-cluster :options="clusterOptions" @clusterclick="click()">
        <l-marker
          v-for="marker in markers"
          :key="marker.index"
          :lat-lng="marker"
          :icon="icon"
        >
          <l-popup>
            <popup-content :data="marker.data"></popup-content>
          </l-popup>
        </l-marker>
      </l-marker-cluster>
    </l-map>
  </div>
</template>

<script>
import L from 'leaflet'
import Vue2Leaflet from 'vue2-leaflet'
import Vue2LeafletMarkerCluster from 'vue2-leaflet-markercluster'
import PopupContent from './PopupContent'
import { mapActions, mapMutations, mapGetters } from 'vuex'
var { LMap, LTileLayer, LMarker, LPopup } = Vue2Leaflet

const LeafIcon = L.divIcon({
  className: 'leaflet-div-WaterLine',
  iconAnchor: [0, 0]
})
export default {
  name: 'mapMode',
  components: { LMap, LTileLayer, LMarker, LPopup, PopupContent, 'l-marker-cluster': Vue2LeafletMarkerCluster },
  props: {
    center: {
      type: Array,
      default: () => [31, 32]
    },
    zoom: {
      type: Number,
      default: 17
    },
    points: {
      type: Array,
      default: () => []
    }
  },
  computed: {
    ...mapGetters({
      device: 'monitor/device'
    }),
    cent: function () {
      if (Array.isArray(this.center)) {
        let lat = this.center[0] || 0
        let lng = this.center[1] || 0
        return [parseFloat(lat), parseFloat(lng)]
      } else {
        return this.center
      }
    },
    markers: function () {
      let rs = []
      let i = 0
      let { list } = this.device
      for (let {lat, lng} of list) {
        rs.push({
          lat: parseFloat(lat),
          lng: parseFloat(lng),
          index: i,
          data: {...list[i]}
        })
        i++
      }
      return rs
    }
  },
  data () {
    return {
      // url: 'http://{s}.tile.osm.org/{z}/{x}/{y}.png',
      // url: 'http://www.google.cn/maps/vt?lyrs=m@189&gl=cn&x={x}&y={y}&z={z}', // google
      url: 'http://webst02.is.autonavi.com/appmaptile?style=7&x={x}&y={y}&z={z}', // 高德
      // url: 'https://api.mapbox.com/styles/v1/mapbox/dark-v9/tiles/256/{z}/{x}/{y}?access_token=pk.eyJ1IjoiemdoMmhoIiwiYSI6ImNqbHQ0bmoybzA2c28zdnFzMXF2c2MxbzUifQ.yoldCvYDDjVtLAQ5eUtM9A',
      // url: 'http://map.geoq.cn/ArcGIS/rest/services/ChinaOnlineStreetPurplishBlue/MapServer/tile/{z}/{y}/{x}',
      attribution:
        '&copy; <a href="http://osm.org/copyright">AutoNavi</a> contributors',
      icon: '',
      clusterOptions: {}
    }
  },
  methods: {
    ...mapActions({
      getDeviceList: 'monitor/getDeviceList',
      getSensorTypes: 'monitor/getSensorTypes',
      getSensorStatus: 'monitor/getSensorStatus',
      configPage: 'monitor/configPage',
      listUpdateByPage: 'monitor/listUpdateByPage'
    }),
    ...mapMutations({
      clearAllData: 'monitor/CLRAR_ALL_DATA'
    }),
    click: function (e) {
      console.log(e)
    }
  },
  mounted () {
    setTimeout(() => {
      console.log('done')
      this.$nextTick(() => {
        this.clusterOptions = { disableClusteringAtZoom: 11 }
      })
    }, 5000)
    this.configPage({'page_number': 1, 'page_size': 9999999})
    this.getDeviceList()
    this.intervalId = window.setInterval(async () => {
      let { list = [] } = this.device
      if (list && list.length > 0) {
        let rs = await this.listUpdateByPage({
          payloadId: list[0].payloadId
        })
        let {list: data, total = 0} = rs.datas
        if (total > 0 && data.length > 0) {
          console.log('数据变化了==============')
          this.getDeviceList()
        }
      }
    }, 3000)
    // this.icon = LeafIcon
  },
  beforeDestroy () {
    window.clearInterval(this.intervalId)
    this.intervalId = null
    // 清楚store中数据
    this.clearAllData()
  },
  destroyed () {
    console.log('destroyed==')
  },
  watch: {
    markers: {
      deep: true,
      handler: function (val, old) {
        this.map = this.$refs.map.mapObject // work as expected
        let bounds = []
        for (let {lat, lng} of this.markers) {
          bounds.push(L.latLng(lat, lng))
        }
        if (bounds.length > 0) {
          this.map && this.map.fitBounds(bounds)
        }
      }
    }
  }
}
</script>

<style lang="less" scoped>
.map {
  height: 100%;
  margin: 0;
  overflow: hidden;
}
</style>




