<template>
  <el-row :gutter="0" class="alarms" style="text-align: left;" v-loading="loading">
    <el-row :gutter="0">
      <el-col :span="6">设备地址: </el-col>
      <el-col :span="18">{{device.title}}</el-col>
    </el-row>
    <el-row v-if="otherStyles.title.showBtry" :gutter="0" v-for="(bt, k) in device.batteries" :key="k">
      <el-col :span="6">电量: </el-col>
      <el-col :span="18"><battery :key="k" :value='bt.percent' :title="bt.tip"></battery></el-col>
    </el-row>
    <el-row :gutter="0" v-if="device.values.length > 0">
      <el-col :span="6">当前值: </el-col>
      <el-col v-for="(val, i) in device.values" :key="i" :span="18 / (device.values.length)" class="data" :title="val.tip">
        <span>{{(val.value.toString().split(".").length === 2 && val.value.toString().split(".")[1].length > 2) ? parseFloat(val.value).toFixed(3) : val.value}}</span>
        <span>{{val.unit}}</span>
      </el-col>
    </el-row>
    <el-row :gutter="0" v-if="device.commands.length > 0">
      <el-col v-for="(command, k) in device.commands" :key="k" :span="24 / (device.commands.length)" :title="command.tip" >
        <el-button round :type="command.cmd == 'ON' ? 'success' : 'danger'" size="mini" @click="switchDev(command)">{{command.name}}</el-button>
      </el-col>
    </el-row>
    <el-row :gutter="0">
      <el-col :span="6">设备编号: </el-col>
      <el-col :span="18">{{device.bottomLeft}}</el-col>
    </el-row>
    <el-row :gutter="0">
      <el-col :span="6">上报时间: </el-col>
      <el-col :span="18">{{device.bottomRight}}</el-col>
    </el-row>
    <el-row :gutter="0">
      <el-col style="text-align: right;">
        <el-button type="primary" size="mini" @click="toDeviceDetail(device)">详情</el-button>
      </el-col>
    </el-row>
    <!-- <sensor-component :params="device" @click.native="toDeviceDetail(device)"></sensor-component> -->
  </el-row>
</template>

<script>
import Sensors from '@/components/sensors/Sensors'
import Battery from '@/components/Battery.vue'
import { mapActions } from 'vuex'

export default {
  name: 'PopupContent',
  components: {
    'sensor-component': Sensors,
    'battery': Battery
  },
  props: {
    data: {
      type: Object,
      default: () => ({ type: '', color: '#ffffff' })
    }
  },
  computed: {
    device: function () {
      return this.data || {}
    },
    otherStyles: function () {
      let style = {}
      if (this.device && this.device.batteries && this.device.batteries.length > 0) {
        style.title = {
          showBtry: true,
          span: [20, 4]
        }
      } else {
        style.title = {
          showBtry: false,
          span: [24, 0]
        }
      }
      return style
    }
  },
  data () {
    return {
      loading: false
    }
  },
  methods: {
    ...mapActions({
      switchDevice: 'monitor/switchDevice'
    }),
    toDeviceDetail (val) {
      let { devId } = val
      this.$router.push({name: 'deviceInfo', query: { devId }})
    },
    switchDev (command) {
      this.loading = true
      this.switchDevice({
        cmd: command.cmd,
        deveui: command.deveui
      }).then(res => {
        this.loading = false
      })
    }
  }
}
</script>

<style lang="less" scoped>
.alarms {
  font-size: 1.3333rem; // 16px
  min-width: 24rem;
  text-align: left;
  line-height: 2rem;
  .label {
    font-size: 1rem; // 12px
    color: #ccc;
    background-color: #000;
  }
}
</style>




