<template>
  <div :class="[equipmentDetail]" :style="{'background-color': statusStyle.bgColor}" v-loading="loading">
    <i class="sensor-icon" :class="sen.icon" :title="sen.title"></i>
    <el-row :gutter="0" :title="sensor.title" class="location">
      <el-col :span="otherStyles.title.span[0]" >
        <span>{{sensor.title}}</span>
      </el-col>
      <el-col v-if="otherStyles.title.showBtry" :span="otherStyles.title.span[1]" class="btry">
        <battery v-for="(bt, k) in sensor.batteries" :key="k" :value='bt.percent' :title="bt.tip"></battery>
      </el-col>
    </el-row>
    <el-row>
      <el-col>
        <el-tag :color="statusStyle.tag.color" style="color: #fff;font-size: 1.6667rem;font-family: Microsoft YaHei">{{statusStyle.tag.text}}</el-tag>
      </el-col>
    </el-row>
    <el-row :gutter="0" :style="{'text-align': 'center', 'line-height': '5.7777rem', 'height': '6.25rem'}" v-if="sensor.values.length > 0">
      <el-col v-for="(val, i) in sensor.values" :key="i" :span="24 / (sensor.values.length)" class="data" :title="val.tip" :style="{'font-size': sensor.values.length <= 2 ? '5rem' : '1.3333rem'}">
        <span :style="{color: statusStyle.tag.color, 'font-family': 'ArialMT'}">{{(val.value.toString().split(".").length === 2 && val.value.toString().split(".")[1].length > 2) ? parseFloat(val.value).toFixed(3) : val.value}}</span>
        <span style="font-size: 0.6667rem;">{{val.unit}}</span>
      </el-col>
    </el-row>
    <el-row :gutter="0" style="text-align: center; line-height: 5.7777rem; height: 6.25rem;" v-if="sensor.commands.length > 0">
      <el-col v-for="(command, k) in sensor.commands" :key="k" :span="24 / (sensor.commands.length)" :title="command.tip" >
        <el-button round :type="command.cmd == 'ON' ? 'success' : 'danger'" size="mini" @click="switchDev(command)">{{command.name}}</el-button>
      </el-col>
    </el-row>
    <el-row :gutter="0" style="text-align: left; font-size:1.16667rem;">
      <el-col :span="10" title="设备编号">{{sensor.bottomLeft}}</el-col>
      <el-col :span="12" :offset="2" title="上报时间" style="text-align: right;">{{sensor.bottomRight}}</el-col>
    </el-row>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import {SENSOR_TYPE} from '@/components/config/sensorType'
import Battery from '@/components/Battery.vue'

const STATUS_MAP = {
  ok: {
    bgColor: '#f3fff9',
    tag: {
      text: '正常',
      color: '#5edf9b'
    }
  },
  offline: {
    bgColor: '#edf2ff',
    tag: {
      text: '离线',
      color: '#618ff9'
    }
  },
  error: {
    bgColor: '#999999',
    tag: {
      text: '故障',
      color: '#666666'
    }
  },
  alarm: {
    bgColor: '#fff4f4',
    tag: {
      text: '报警',
      color: '#ff5b5b'
    }
  }
}

export default {
  name: 'sensors',
  components: {
    'battery': Battery
  },
  props: {
    params: {
      type: Object,
      required: true,
      default: () => {
        return {}
      }
    }
  },
  computed: {
    sen: function () {
      let {label: title, icon, bgClass} = SENSOR_TYPE[this.sensor.categoryCode] || {
        label: '传感器',
        icon: 'iconfont icon-yangantanceqi',
        bgClass: 'smoke-detector'
      }
      return {
        icon,
        title,
        bgClass
      }
    },
    otherStyles: function () {
      let style = {}
      if (this.sensor.batteries && this.sensor.batteries.length > 0) {
        style.title = {
          showBtry: true,
          span: [20, 4]
        }
      } else {
        style.title = {
          showBtry: false,
          span: [24, 0]
        }
      }
      return style
    },
    statusStyle: function () {
      const MAP = {
        '正常': 'ok',
        '离线': 'offline',
        '故障': 'error',
        '报警': 'alarm'
      }
      const { statusName } = this.sensor
      return STATUS_MAP[MAP[statusName]]
    }
  },
  data () {
    return {
      loading: false,
      sensor: {
        bottomLeft: '',
        bottomRight: '',
        categoryCode: '',
        categoryName: '',
        devId: '',
        statusName: '',
        title: '',
        batteries: [],
        values: [],
        commands: []
      },
      equipmentDetail: 'equipment-detail'
    }
  },
  methods: {
    ...mapActions({
      switchDevice: 'monitor/switchDevice'
    }),
    switchDev (command) {
      this.loading = true
      this.switchDevice({
        cmd: command.cmd,
        deveui: command.deveui
      }).then(res => {
        this.loading = false
      })
    }
  },
  watch: {
    params: {
      immediate: true,
      deep: true,
      handler: function (val, old) {
        if (val) {
          let {bottomLeft, bottomRight, categoryCode, categoryName, devId, statusName, title, batteries, values, commands} = val
          this.sensor = Object.assign({}, this.sensor, {
            bottomLeft,
            bottomRight,
            categoryCode,
            categoryName,
            devId,
            statusName,
            title,
            batteries,
            values,
            commands
          })
        }
      }
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/style/variable.less';
.equipment-detail {
  box-sizing: border-box;
  position: relative;
  height: 18rem;
  padding: 5px;
  font-size: 1.5rem;
  line-height: 4rem;
  color: @common-font-color;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.12,0 0 6px rgba(0,0,0,0.04));
  border: solid 1px @common-border-color;
  border-radius: 4px;
  cursor: default;
  .sensor-icon {
    position: absolute;
    right: 0.8333rem;
    bottom: 3rem;
    font-size: 4rem;
    z-index: 1;
  }
  .location {
    line-height: 1.5rem;
    height: 3rem;
  }
  .btry {
    text-align: right;
    line-height: 1.5rem;
  }
  .data {
    // font-size: 2.3333rem; // 28px
    white-space: nowrap;
  }
}
.red {
  color: red;
}
.yellow {
  color: yellow;
}
.error {
  color: #2D3132;
}
</style>





